.main_featured_inner{width:100%}.featured_title{position:relative;padding-bottom:40px;font-size:18px;font-weight:500;text-align:center;letter-spacing:1px}.featured_title::after{position:absolute;width:50px;height:12px;content:"";bottom:0;left:50%;margin-left:-25px;margin-bottom:15px;background-image:url(//cdn.shopify.com/s/files/1/1357/1399/t/4/assets/line_title.png?v=1614387…)}.featured_content_wrapper{margin:0 15px 30px}.content_nav{border-top:1px solid #dbdbdb}.content_nav_item{position:relative;display:inline-block;padding-top:28px;margin-right:35px;font-weight:600;line-height:1em;color:#9d9d9d;opacity:.8}.content_nav_item:hover{cursor:pointer;color:#2f93a3;background-color:rgba(0,0,0,0);opacity:1}.content_nav_item:hover::after{content:"";position:absolute;width:100%;height:2px;margin-top:-2px;top:0;left:0;background-color:#2f93a3;animation:bod .3s ease-in-out}@keyframes bod{0%{transform:scale(0)}100%{transform:scale(1)}}.active{color:#2f93a3;background-color:rgba(0,0,0,0);opacity:1}.active::after{content:"";position:absolute;width:100%;height:2px;margin-top:-2px;top:0;left:0;background-color:#2f93a3}.featured_product{width:100%}.product_wrapper{position:relative;display:inline-block;width:260px;height:391px;margin:0 16px;border:1px solid rgba(0,0,0,0);overflow:hidden}.product_wrapper:hover{cursor:pointer;border:1px solid #2f93a3;overflow:hidden}.product_wrapper:hover .img_hover,.product_wrapper:hover .product_icons{transition:.4s;visibility:visible;opacity:1;transform:translateX(-258px)}.product_wrapper:hover .img_default,.product_wrapper:hover .product_info{transition:.4s;transform:scale(0.4);opacity:0}.product_wrapper:hover .product_icons{transform:translate(0, -72px)}.product_image{width:100%;display:flex;margin-bottom:26px}.img_default,.img_hover{width:100%;height:100%;object-fit:cover}.img_default{transition:.7s;transform:scale(1)}.img_hover{visibility:hidden;opacity:0;transform:translateX(-400px);transition:.4s}.product_info{text-align:center;font-size:14